import Decorator from '../Decorator';
import Status from '../Status';

/**
 * @callback module:jibo/bt/decorators.TimeoutSucceedJs~GetTime
 * @returns {number} Time in milliseconds.
 */

/**
 * @class TimeoutSucceedJs
 * @extends module:jibo/bt.Decorator
 * @memberof module:jibo/bt/decorators
 * @description
 * `TimeoutSucceedJs` forces the behavior it's decorating to succeed after the specified amount of time.
 * @param {Object} options See {@link module:jibo/bt.Decorator|Decorator} for all options.
 * @param {module:jibo/bt/decorators.TimeoutSucceedJs~GetTime} options.getTime - Time in milliseconds until `TimeoutJs` forces the behavior it's decorating to succeed.
 */
export default class TimeoutSucceedJs extends Decorator {
	start() {
		this.startTime = new Date().getTime();
		this.timeout = this.options.getTime();
		return true;
	}
	update(result) {
		var elapsed = new Date().getTime() - this.startTime;
		if(elapsed > this.timeout) {
			return Status.SUCCEEDED;
		}
		return result;
	}
}